import { Component, OnInit, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { UserService } from '../services/user.service';


@Component({
  selector: 'app-hsignup',
  templateUrl: './hsignup.component.html',
  styleUrls: ['./hsignup.component.css']
})


export class HsignupComponent implements OnInit {


  HregisterForm: FormGroup;
  isLoading: boolean;
  constructor(private _router: Router,
    private fb: FormBuilder, private el: ElementRef, private user: UserService) { }

  ngOnInit() {
    this.createRegisterForm();
  }
  onSubmit() {
    this.openSpinner();
    this.register();
  }
  openSpinner() {
    this.isLoading = true;
    setTimeout(() => {
      this.isLoading = false;
      this.login();
    }, 5000);
  }
  login() {
    this._router.navigate(['/home/login']);
  }
  createRegisterForm() {
    this.HregisterForm = this.fb.group({
      name: ['', [Validators.required, Validators.minLength(3), Validators.pattern('[a-zA-Z]*')]],
      hid: ['', [Validators.required, Validators.minLength(12)]],
      password: ['', [Validators.required, Validators.minLength(3)]],
      confirmPassword: ['', Validators.required],
      location: ['', Validators.required],
      contact: ['', [Validators.required, Validators.pattern("^[0-9]{10}")]],
      profilePic: ['', Validators.required],
      role:['hospital']
    }, { validator: [this.fileUploadType, this.passwordMatchValidator] });
  }
  passwordMatchValidator(g: FormGroup) {
    return g.get('password').value === g.get('confirmPassword').value ? null : { 'mismatch': true };
  }

  fileUploadType(g: FormGroup) {
    let file = g.get('profilePic').value;
    if (file) {
      const extension = file.split('.');
      let val = extension[extension.length - 1];

      if (val === "jpeg" || val === "png" || val === "jpg") {
        return null;
      }
      else return { 'fileextension': true }
    }
    return null;
  }
  register() {

    //   // this.openSpinner(true);
    let formData = new FormData();

    //locate the file element meant for the file upload.
    let inputEl: HTMLInputElement = this.el.nativeElement.querySelector('#profilePic');
    //get the total amount of files attached to the file input.
    let fileCount: number = inputEl.files.length;
    //check if the filecount is greater than zero, to be sure a file was selected.
    if (fileCount > 0) { // a file was selected
      //append the key name 'photo' with the first file in the element
      formData.append('photo', inputEl.files.item(0));
    }
    // formData.append('name', this.HregisterForm.value.name);
    // formData.append('email', this.HregisterForm.value.email);
    // formData.append('password', this.HregisterForm.value.password);
    this.user.registertion(this.HregisterForm.value).subscribe(data => console.log(data, "Successfully submitted!"), error => console.log(error));
  }
}
