import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { UserService } from '../services/user.service';
import { AlertService } from '../services/alert.service';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-view-patients',
  templateUrl: './view-patients.component.html',
  styleUrls: ['./view-patients.component.css']
})
export class ViewPatientsComponent implements OnInit {
  donors: any;
  avail: string;
  attending: boolean;
  isLoading: boolean;
  public text = "text-center";
  ignored: boolean;
  submitted: boolean;
  message1: any;
  message2: any;

  constructor(private router: Router, private user: UserService, private snackBar: MatSnackBar, private alert: AlertService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.paramMap.subscribe((params: ParamMap) => {
      let val = params.get('value');
      this.avail = val;
    })
    this.reloadData(this.avail);
  }

  reloadData(avail) {
    this.user.getDonorList(avail).subscribe(data => {
      this.donors = data,
        this.submitted = true;
    });
  }

  send(val, msg1, msg2) {
    this.message1 = msg1;
    this.message2 = msg2;
    this.user.sendNotification(val);
    this.openSpinner(val);
  }

  openSpinner(val) {
    this.isLoading = true;
    console.log(val.name);
    setTimeout(() => {
      this.isLoading = false;
      if (this.submitted == true) {
        this.openSnackBar("Message sent to " + val.name + "  !", 'Dismiss');
      }
    }, 4000);
    this.get();
  }

  openSnackBar(message, action) {
    this.snackBar.open(message, action);
  }

  get() {
    this.alert.requestSource.subscribe(data => {
      this.attending = data
    });
    if (this.attending == true) {
      this.attending = true;
    }
    if (this.attending == false) {
      this.ignored = true;
    }
  }

}
