import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from '../services/user.service';
 

@Component({
  selector: 'app-request',
  templateUrl: './request.component.html',
  styleUrls: ['./request.component.css']
})
export class RequestComponent implements OnInit {
  requestForm: FormGroup;
  isLoading: boolean;
  minDate=new Date();
  year=this.minDate.getFullYear();
  month=this.minDate.getMonth();
  day=this.minDate.getDate()+10; //only 10 days requirement for emergency operation 
  maxDate=new Date(this.year,this.month,this.day);

  
  constructor(private fb: FormBuilder, private router: Router, private route: ActivatedRoute, private user: UserService) {}
  ngOnInit() { 
    this.sendRequestForm();
   }
   goback(){
     window.history.back();
   }
   sendRequestForm(){
    this.requestForm = this.fb.group({
      name: ['', [Validators.required, Validators.minLength(3)]],
      contact: ['', [Validators.required, Validators.pattern('^[0-9]{10}$')]],
      id: ['', [Validators.required, Validators.pattern('^[0-9]{12}$')]],
      pname: ['', [Validators.required, Validators.pattern('[a-zA-Z]*')]],
      page: ['', [Validators.required, Validators.maxLength(2)]],
      gender: ['', [Validators.required]],
      type: ['bdonor'],
      requestFor: ['', [Validators.required]],
      bloodGroup: ['', Validators.required],
      units: ['', Validators.required],
      location: ['', [Validators.required, Validators.minLength(3)]],
      requiredDate: ['', [Validators.required]]
    });
   }
  saveRequest() {
    this.openSpinner();
    if (navigator.geolocation)
      navigator.geolocation.getCurrentPosition(function (position) {
        //  $.get("http://maps.googleapis.com/maps/api/geocode/json?latlng="+position.coords.latitude+","+position.coords.longitude+"&sensor=true",function(data){
        //   console.log(data);
        //  })
      });
    else {
      console.log("location is not supported");
    }
    this.user.request(this.requestForm.value).subscribe(data => console.log(data, "Successfully submitted!"), error => console.log(error));
  }

  openSpinner() {
    const value = this.requestForm.value.type;
    this.isLoading = true;
    setTimeout(() => {
      this.isLoading = false;
      this.login(value);
    }, 5000);
  }

  login(value) {
    this.router.navigate([value], { relativeTo: this.route });
  }
}