import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { EmergencyComponent } from './emergency/emergency.component';
import { BlooddonationComponent } from './blooddonation/blooddonation.component';
import { OrgandonationComponent } from './organdonation/organdonation.component';
import { NewsComponent } from './news/news.component';
import { WhytodonateComponent } from './whytodonate/whytodonate.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { ContactusComponent } from './contactus/contactus.component';
import { FaqComponent } from './faq/faq.component';
import { RequestComponent } from './request/request.component';
import { OrequestComponent } from './orequest/orequest.component';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { ViewPatientsComponent } from './view-patients/view-patients.component';
import { HsignupComponent } from './hsignup/hsignup.component';
import { DonordboardComponent } from './donordboard/donordboard.component';
import { AuthGuard } from './auth.guard';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'home/login', component: LoginComponent },
  { path: 'home/login/dashboard', component: DonordboardComponent, canActivate: [AuthGuard] },
  { path: 'home/login/dashboard/news', component: NewsComponent, canActivate: [AuthGuard] },
  { path: 'home/login/signup', component: SignupComponent },
  { path: 'home/signup', component: SignupComponent },
  { path: 'home/emergency', component: EmergencyComponent },
  { path: 'home/blooddonation', component: BlooddonationComponent },
  { path: 'home/organdonation', component: OrgandonationComponent },
  { path: 'home/news', component: NewsComponent },
  { path: 'home/whytodonate', component: WhytodonateComponent },
  { path: 'home/aboutus', component: AboutusComponent },
  { path: 'home/contactus', component: ContactusComponent },
  { path: 'home/faq', component: FaqComponent },
  { path: 'home/emergency/request', component: RequestComponent },
  { path: 'home/emergency/request/:value', component: ViewPatientsComponent },
  { path: 'home/emergency/orequest', component: OrequestComponent },
  { path: 'home/emergency/orequest/:value', component: ViewPatientsComponent },
  { path: 'home/blooddonation/signup', component: SignupComponent },
  { path: 'home/organdonation/signup', component: SignupComponent },
  { path: 'home/hsignup', component: HsignupComponent },
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [HomeComponent, EmergencyComponent, BlooddonationComponent, OrgandonationComponent, WhytodonateComponent, AboutusComponent, ContactusComponent, FaqComponent, NewsComponent, RequestComponent, OrequestComponent, LoginComponent, SignupComponent, PageNotFoundComponent]
