import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { throwError, Observable, BehaviorSubject, Subject } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Donor } from '../models/donor';

@Injectable({ providedIn: 'root' })
export class UserService {

  private datatxt: BehaviorSubject<any>;
  public notificationSource: Observable<any>;

  private notifCount: BehaviorSubject<any>;
  public notifObserve$: Observable<any>;
  public notif: number;

  constructor(private http: HttpClient) {
    this.datatxt = new BehaviorSubject<any>('default Notification');
    this.notificationSource = this.datatxt.asObservable();
    // this.notifCount=new BehaviorSubject<any>(0);
    // this.notifObserve$=this.notifCount.asObservable();    //FOR NOTIFICATION COUNT
  }

  sendNotification(data: any) {
    this.datatxt.next(data);
  }

  // getCount(value:number){
  //   this.notifCount.next(value);   //FOR NOTIFICATION COUNT
  // }

  count() {
    return this.http.get(`/users/count`);  //APPLICATION-FORMS COUNT
  }

  registerData(user): Observable<any> {
    return this.http.post(`/users/register`, user).pipe(catchError(e => this.handleError(e))); //USER-SIGNUP
  }

  registertion(data): Observable<any> {
    return this.http.post(`/users/hospital`, data).pipe(catchError(e => this.handleError(e)));  //HOSPITAL-SIGNUP
  }

  getDonorList(type: String) {
    return this.http.get(`/donor/donorList/${type}`);    //DONORS-LIST BY TYPE
  }

  getReq(type: String): Observable<any> {
    return this.http.get(`/donor/getReq/${type}`).pipe(catchError(e => this.handleError(e))); //RECIEVED-NOTIF MESSAGE
  }

  delRequest(id: any, type: String): Observable<any> {
    return this.http.delete(`/donor/delReq/${id}/${type}`).pipe(catchError(e => this.handleError(e)));  //ACCEPTED-REQUEST BY DONOR 
  }


  sendForm(data): Observable<any> {
    return this.http.post(`/users/app`, data).pipe(catchError(e => this.handleError(e)));  //APPLICATION-FORM DATA
  }

  getAppointments() {
    return this.http.get(`/donor/appointment`).pipe(catchError(e => this.handleError(e)));  //GET ALL APPOINTMENTS
  }

  sendContact(contact): Observable<any> {
    return this.http.post(`/users/contact`, contact);  //CONTACT-FORM DATA
  };

  request(req) {
    return this.http.post(`/users/request`, req).pipe(catchError(e => this.handleError(e))); //B-REQUEST DATA
  }

  orequest(req) {
    return this.http.post(`/users/orequest`, req).pipe(catchError(e => this.handleError(e)));  //O-REQUEST DATA
  }

  private handleError(err: HttpErrorResponse) {
    console.log(err)
    console.log("Handle Http calls error");
    console.log(err.error.message);
    return throwError(err.error);
  }
}