import { keyframes, style } from '@angular/animations';

export const swing = [
    style({ transform: 'rotate3d(0,0,1,15deg)', offset: .2 }),
    style({ transform: 'rotate3d(0,0,1,-10deg)', offset: .4 }),
    style({ transform: 'rotate3d(0,0,1,5deg)', offset: .6 }),
    style({ transform: 'rotate3d(0,0,1,-5deg)', offset: .8 }),
    style({ transform: 'none', offset: 1 }),
]

export const jello = [
    style({ transform: 'skewX(-12.5deg) skewY(-12.5deg)', offset: .22 }),
    style({ transform: 'skewX(6.25deg) skewY(6.25deg)', offset: .33 }),
    style({ transform: 'skewX(-3.125deg) skewY(-3.125deg)', offset: .44 }),
    style({ transform: 'skewX(1.5625deg) skewY(1.5625deg)', offset: .55 }),
    style({ transform: 'skewX(-0.78125deg) skewY(-0.78125deg)', offset: .66 }),
    style({ transform: 'skewX(0.390625deg) skewY(0.390625deg)', offset: .77 }),
    style({ transform: 'skewX(-0.1953125deg) skewY(-0.1953125deg)', offset: .88 }),
    style({ transform: 'none', offset: 1 })
]

export const pulse = [
    style({ transform: 'scale3d(1.05, 1.05, 1.05)', offset: .5 }),
    style({ transform: 'none', offset: 1 })
]

export const rubberBand=[
    style({transform: 'scale3d(1.25, 0.75, 1)',offset:.3}),
    style({transform: 'scale3d(0.75, 1.25, 1)',offset:.4}),
    style({transform: 'scale3d(1.15, 0.85, 1)',offset:.5}),
    style({transform: 'scale3d(0.95, 1.05, 1)',offset:.65}),
    style({transform: 'scale3d(1.05, 0.95, 1)',offset:.75}),
    style({ transform: 'none', offset: 1 })
]