import { Component, OnInit } from '@angular/core';
import {trigger, keyframes, animate, transition} from '@angular/animations';
import * as kf from './keyframes'; 

@Component({
  selector: 'app-organdonation',
  templateUrl: './organdonation.component.html',
  styleUrls: ['./organdonation.component.css'],
  animations: [   
    trigger('cardAnimator', [
      transition('* => tada', animate(1000, keyframes(kf.tada))),
      transition('* => bounceIn', animate(1000, keyframes(kf.bounceIn))),
      transition('* => bounceOut', animate(1000, keyframes(kf.bounceOut))),
    ]),
  ]
})
export class OrgandonationComponent implements OnInit {

  goback(){
    window.history.back();
  }
  animationState: String;

  startAnimation(state) {
    if (!this.animationState) {
      this.animationState = state;
    }
  }
  resetAnimationState() {
    this.animationState='';
  }

  ngOnInit() {
  }

}
