export default "@import url('https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap');\r\n/* MAIN BACKGROUND  */\r\nbody{\r\n    background:#ecf0f3;\r\n}\r\n/* TEXT STYLES */\r\nh2{\r\n    font-family:'Major Mono Display', monospace;\r\n    font-weight:bolder;\r\n    font-size:40px;\r\n    letter-spacing: 0.5px;\r\n}\r\n/* FORM STYLES */\r\n.login-div{\r\n    border-radius:40px;\r\n    background:#ecf0f3;\r\n    box-shadow:13px 13px 20px #cbced1,\r\n               -13px -13px 20px #ffffff \r\n}\r\nimg{\r\n    box-shadow:0px 0px 2px #5f5f5f,\r\n               0px 0px 0px 5px #ecf0f3,\r\n               8px 8px 15px #a7aaaf,\r\n               -8px -8px 15px #ffffff \r\n}\r\n.form{\r\n    padding:20px;\r\n}\r\n.form-group{\r\n    border-radius:25px;\r\n    box-shadow:inset 8px 8px 8px #cbced1,inset -8px -8px 8px #ffffff; \r\n}\r\n.form-group input{\r\n    border:none;\r\n    outline:none;\r\n    background:none;\r\n    font-size:18px;\r\n    height: 55px;\r\n    color:#555;\r\n    padding:20px 10px 20px 20px;\r\n}\r\n/* FORM BUTTON EFFECT */\r\n.login-btn,.g-button{\r\n    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;\r\n    font-size:18px;\r\n    font-weight:800;\r\n    color:#595959;\r\n    background:#dde1e7;\r\n    margin:15px 0;\r\n    width:100%;\r\n    height:45px;\r\n    border:none;\r\n    outline:none;\r\n    cursor:pointer;\r\n    border-radius:25px;\r\n    box-shadow:2px 2px 5px #cbced1,-5px -5px 10px #ffffff;\r\n}\r\n.login-btn:focus,.g-button:focus{\r\n    box-shadow:inset 2px 2px 5px #babeec,inset -5px -5px 10px #ffffff73;\r\n}\r\n/* BUTTON ICONS */\r\nspan{\r\n    font-size:20px;\r\n}\r\n\r\n\r\n\r\n\r\n\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"