import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { AuthService } from './../services/auth.service';
import { AuthenticationService } from '../services/authentication.service';
import { Subscription, Observable } from 'rxjs';
import { Chart } from 'node_modules/chart.js';
import { MatDialog } from '@angular/material';
import { DprofileComponent } from '../dprofile/dprofile.component';
import { AppformComponent } from '../appform/appform.component';
import { NotifyComponent } from '../notify/notify.component';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-donordboard',
  templateUrl: './donordboard.component.html',
  styleUrls: ['./donordboard.component.css'],
  providers: [AngularFirestore, AngularFireAuth, AuthService]
})
export class DonordboardComponent implements OnInit {
  currentUserSubscription: Subscription;
  currentUser;
  opened = false;
  value: any;
  notifCount: number;
  appdata: any;
  Hname:any;
  public text="text-center"

  constructor(private router: Router, public auth: AuthService, public authentication: AuthenticationService
    , public dialog: MatDialog, public user: UserService) {
      
    this.currentUserSubscription = this.authentication.currentUser.subscribe(user => {
      this.currentUser = user
  });
  if(this.currentUser.role == 'hospital'){
    this.user.getAppointments().subscribe(data => { this.appdata=data,this.Hname=this.appdata[0].hospital },error => console.log(error));
  }
  }

  ngOnInit() {
    this.showchart();
    this.nextChart();
    this.count();
    this.notifCount = 1;
  }

  openProfile() {
    this.dialog.open(DprofileComponent);
  }
  showNotify() {
    this.notifCount = 0;
    this.dialog.open(NotifyComponent);
  }
  bookAppointment() {
    this.dialog.open(AppformComponent);
  }
  count() {
    this.user.count().subscribe(count => { this.value = count }, error => console.log(error));
  }
  signOut() {
    if (this.auth.user) {
      this.auth.signOut();
    }
    if (this.currentUser) {
      this.authentication.logout();
      this.router.navigate(['/home/login']);
    }
  }

  showchart() {
    var ctx = document.getElementById('myChart');
    var chart = new Chart(ctx, {
      // The type of chart we want to create
      type: 'bar',

      // The data for our dataset
      data: {
        labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
        datasets: [{
          label: 'Patients Attended',
          backgroundColor: 'rgb(255,134, 53)',
          borderColor: 'rgb(255,134, 53)',
          data: [5, 10, 15, 2, 40, 22, 30, 28, 12, 25, 50, 80]
        }]
      },

      // Configuration options go here
      options: {}
    });
  }
  nextChart() {

    var ctx = document.getElementById('chart1');
    var chart = new Chart(ctx, {
      // The type of chart we want to create
      type: 'line',

      // The data for our dataset
      data: {
        labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
        datasets: [{
          label: 'Attended Events',
          backgroundColor: 'rgb(255,80, 80)',
          borderColor: 'rgb(255,80, 80)',
          data: [0, 10, 5, 33, 25, 28, 40, 12, 25, 50, 80, 35]
        }]
      },
      // Configuration options go here
      options: {}
    });

  }

}




