// COMPONENTS
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule, routingComponents } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { MaterialModule } from './material/material.module';
import { BlooddonationComponent } from './blooddonation/blooddonation.component';
import { OrgandonationComponent } from './organdonation/organdonation.component';
import { NewsComponent } from './news/news.component';
import { WhytodonateComponent } from './whytodonate/whytodonate.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { ContactusComponent } from './contactus/contactus.component';
import { FaqComponent } from './faq/faq.component';
import { OrequestComponent } from './orequest/orequest.component';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { ViewPatientsComponent } from './view-patients/view-patients.component';
import { HsignupComponent } from './hsignup/hsignup.component';
import { DonordboardComponent } from './donordboard/donordboard.component';
import { DprofileComponent } from './dprofile/dprofile.component';
import { AppformComponent } from './appform/appform.component';
import { NotifyComponent } from './notify/notify.component';
import { AgmCoreModule } from '@agm/core';
import { AuthGuard } from './auth.guard';
import { AngularFireModule } from '@angular/fire';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';

import * as Hammer from 'hammerjs';
import { HammerGestureConfig, HAMMER_GESTURE_CONFIG } from '@angular/platform-browser';
import { environment } from 'src/environments/environment.prod';

export class MyHammerConfig extends HammerGestureConfig {
  overrides = <any>{
    'swipe': { direction: Hammer.DIRECTION_ALL }
  }
}
// FIREBASE AUTH CONFIG
const firebaseConfig = environment.Auth

@NgModule({
  declarations: [
    AppComponent,
    routingComponents,
    BlooddonationComponent,
    OrgandonationComponent,
    ViewPatientsComponent,
    NewsComponent,
    WhytodonateComponent,
    AboutusComponent,
    ContactusComponent,
    FaqComponent,
    OrequestComponent,
    LoginComponent,
    SignupComponent,
    HsignupComponent,
    DonordboardComponent,
    DprofileComponent,
    AppformComponent,
    NotifyComponent,
    PageNotFoundComponent
  ],
  entryComponents: [DprofileComponent, AppformComponent, NotifyComponent],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    MaterialModule,
    AgmCoreModule.forRoot({
      apiKey: environment.api.key
    }),
    AngularFireModule.initializeApp(firebaseConfig)
  ],
  providers: [AuthGuard, AngularFirestore, AngularFireAuth, {
    provide: HAMMER_GESTURE_CONFIG,
    useClass: MyHammerConfig
  }],
  bootstrap: [AppComponent]
})
export class AppModule { }

