import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from '../services/user.service';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-appform',
  templateUrl: './appform.component.html',
  styleUrls: ['./appform.component.css']
})
export class AppformComponent implements OnInit {
  requestForm: FormGroup;
  route: Router;
  isLoading: boolean;
  submitted: boolean;
  message: String;
  count: any;

  constructor(private fb: FormBuilder, private snackBar: MatSnackBar, private router: Router, private user: UserService) {
    this.requestForm = this.fb.group({
      name: ['', [Validators.required, Validators.pattern('[a-zA-Z]*')]],
      email: ['', Validators.required],
      contact: ['', [Validators.required, Validators.pattern('^[0-9]{10}$')]],
      gender: ['', [Validators.required]],
      location: ['', Validators.required],
      age: ['', [Validators.required, Validators.maxLength(2)]],
      hospital: ['', Validators.required],
      type: ['', Validators.required],
      bloodGroup: ['', Validators.required],
      date: ['', Validators.required]
    });
  }

  saveRequest() {
    this.openSpinner();
    this.user.sendForm(this.requestForm.value).subscribe(data => console.log(data, "Successfully submitted!", this.submitted = true), error => console.log(error, this.submitted = false));
  }

  openSpinner() {
    this.isLoading = true;
    setTimeout(() => {
      this.isLoading = false;
      if (this.submitted == true) {
        this.openSnackBar("Form Submitted", 'Dismiss');
      }
    }, 1000);
  }

  openSnackBar(message, action) {
    this.snackBar.open(message, action)
  }


  ngOnInit() {
  }

}
