import { Component, OnInit } from '@angular/core';
import { Contact } from '../models/contact';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from '../services/user.service';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-contactus',
  templateUrl: './contactus.component.html',
  styleUrls: ['./contactus.component.css']
})
export class ContactusComponent implements OnInit {

  contact: Contact = new Contact();

  submitted = false;
  isLoading: boolean;

  constructor(private route: Router, private user: UserService,private snackBar: MatSnackBar) { }

  goback(){
    window.history.back();
  }
  
  con(): void {
    this.submitted = false;
    this.contact = new Contact();
  }

  save() {
    this.openSpinner();
    this.user.sendContact(this.contact).subscribe(data => console.log(data, this.submitted = true), error => { console.log(error), this.submitted = false });
  }

  ngOnInit() {
  }

  openSpinner() {
    this.isLoading = true;
    setTimeout(() => {
      this.isLoading = false;
      if (this.submitted == true) {
        this.openSnackBar("Message Submitted", 'Dismiss');
      }
    }, 1000);
  }
  openSnackBar(message, action) {
    this.snackBar.open(message, action)
    if(action=='Dismiss'){
      this.route.navigate(['/home']);
    }
  }
  onSubmit() {
    this.save();
  }
}
