import { Component, OnInit } from '@angular/core';
import {trigger, keyframes, animate, transition} from '@angular/animations';
import * as kf from './keyframes';

@Component({
  selector: 'app-blooddonation',
  templateUrl: './blooddonation.component.html',
  styleUrls: ['./blooddonation.component.css'],
  animations: [   
    trigger('cardAnimator', [
      transition('* => tada', animate(1000, keyframes(kf.tada))),
      transition('* => fadeL', animate(1000, keyframes(kf.fadeLeft))),
      transition('* => fadeR', animate(1000, keyframes(kf.fadeRight))),
      transition('* => jello', animate(1000, keyframes(kf.jello)))
    ]),
  ]
})
export class BlooddonationComponent implements OnInit {

  latitude=17.4274;
  longitude=78.4189;
  // (mapClick)=Needs Billing account

  goback(){
    window.history.back();
  }

  animationState: String;

  startAnimation(state) {
    if (!this.animationState) {
      this.animationState = state;
    }
  }
  resetAnimationState() {
    this.animationState='';
  }

  ngOnInit() {
  }

onChooseLoc(event){
console.log(event);
}

}
