import { Component, OnInit, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup,Validators, FormBuilder } from '@angular/forms'; 
import { UserService } from '../services/user.service';


@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  registerForm: FormGroup;
  isLoading: boolean;

  constructor(private _router: Router,
    private fb: FormBuilder, private el: ElementRef, private user: UserService) { }

  ngOnInit() {
    this.createRegisterForm();
  }

  onSubmit() {
    // if (navigator.geolocation)
    //   navigator.geolocation.getCurrentPosition(function (position) {
    //     this.latitude=position.coords.latitude;
    //     this.longitude=position.coords.longitude;          //GMAPS API-BILLING ACCOUNT
    //   });
    // else {
    //   console.log("location is not supported");
    // }
    this.openSpinner();
    this.register();
  }

  createRegisterForm() {
    this.registerForm = this.fb.group({
      name: ['', [Validators.required, Validators.pattern('[a-zA-Z]*')]],
      email: ['', [Validators.email, Validators.required]],
      password: ['', Validators.required],
      confirmPassword: ['', Validators.required],
      type: ['', Validators.required],
      bgroup: ['', Validators.required],
      location: ['', [Validators.required, Validators.minLength(3)]],
      contact: ['', [Validators.required, Validators.maxLength(10), Validators.pattern('^[0-9]{10}')]],
      age: ['', Validators.required],
      profilePic: ['', Validators.required],
      role:['user']
    },
      { validator: [this.fileUploadType, this.passwordMatchValidator, this.ageMax] });
  };

  ageMax(g: FormGroup) {
    if (g.get('age').value <= 18 || g.get('age').value >= 60) {
      return { 'age': true };
    }
    else {
      return null;
    }
  }

  passwordMatchValidator(g: FormGroup) {
    return g.get('password').value === g.get('confirmPassword').value ? null : { 'mismatch': true };
  }

  fileUploadType(g: FormGroup) {
    let file = g.get('profilePic').value;
    if (file) {
      const extension = file.split('.');
      let val = extension[extension.length - 1];

      if (val === "jpeg" || val === "png" || val === "jpg") {
        return null;
      }
      else return { 'fileextension': true }
    }
    return null;
  }
  
  openSpinner() {
    this.isLoading = true;
    setTimeout(() => {
      this.isLoading = false;
      this.login();
    }, 5000);
  }//end of openSpinner function
  
  login() {
    this._router.navigate(['/home/login']);
  }
  
  //on login navbar link click
  register() {
    let formData = new FormData();
    //locate the file element meant for the file upload.
    let inputEl: HTMLInputElement = this.el.nativeElement.querySelector('#profilePic');
    //get the total amount of files attached to the file input.
    let fileCount: number = inputEl.files.length;
    //check if the filecount is greater than zero, to be sure a file was selected.
    if (fileCount > 0) { // a file was selected
      //append the key name 'photo' with the first file in the element
      formData.append('photo', inputEl.files.item(0));
    }
    this.user.registerData(this.registerForm.value).subscribe(data => data, error => console.log(error));
  }
}
