import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { AuthService } from './../services/auth.service';
import { AuthenticationService } from '../services/authentication.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-dprofile',
  templateUrl: './dprofile.component.html',
  styleUrls: ['./dprofile.component.css'],
  providers: [AngularFirestore, AngularFireAuth, AuthService]
})
export class DprofileComponent implements OnInit {
  currentUserSubscription: Subscription;
  currentUser;
  constructor(private router: Router, public auth: AuthService, public authentication: AuthenticationService) {
    this.currentUserSubscription = this.authentication.currentUser.subscribe(user => {
      this.currentUser = user;
    })
  }

  ngOnInit() {
  }

}
