import { Component, OnInit, Input } from '@angular/core';
import { UserService } from '../services/user.service';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../services/authentication.service';
import { Subscription, Observable } from 'rxjs';
import { AlertService } from '../services';


@Component({
  selector: 'app-notify',
  templateUrl: './notify.component.html',
  styleUrls: ['./notify.component.css']
})
export class NotifyComponent implements OnInit {
  currentUserSubscription: Subscription;
  currentUser;
  value;
  avail: any;
  attending: any;
  count: number = 0;
  request$: Observable<any>;

  constructor(private router: Router, private user: UserService, private alert: AlertService, public authentication: AuthenticationService) {

    this.currentUserSubscription = this.authentication.currentUser.subscribe(user => {
      this.currentUser = user;   //authenticated user data
    })

    this.user.notificationSource.subscribe(data => {
      this.value = data;      //sender data
    });
  }

  ngOnInit() {
    this.compareUser();
  }

  compareUser() {
    if (this.currentUser.name === this.value.name && this.currentUser.email === this.value.email && this.currentUser.contact === this.value.contact && this.currentUser.location === this.value.location) {
      this.avail = this.value.type;
      this.request$ = this.user.getReq(this.avail); //recieved message from patient
      this.count++;
      // this.user.getCount(this.count);

    }
  }

  attendRequest(value1, value2) {
    this.attending = true;
    this.alert.sentRequest(this.attending);
    this.user.delRequest(value1, value2).subscribe(data => { console.log(data), err => console.log(err) });
  }

  ignoreRequest() {
    this.attending = false;
    this.alert.sentRequest(this.attending);
  }

}
